.box {
  width: 8rem;
  height: 8rem;
  color: red;
  position: relative;
}
.box::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    z-index: -1;
    width: 200%;
    height: 200%;
    -webkit-transform: scale(0.5, 0.5);
            transform: scale(0.5, 0.5);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    box-sizing: border-box;
    border: 1px solid #ccc;
}
@media screen and (-webkit-min-device-pixel-radio: 3) {
.box::before {
        -webkit-transform: scale(0.33333, 0.33333);
                transform: scale(0.33333, 0.33333);
}
}
@media screen and (-webkit-min-device-pixel-radio: 2) {
.box::before {
        -webkit-transform: scale(0.55556, 0.55556);
                transform: scale(0.55556, 0.55556);
}
}



